import styled from "styled-components";
import {
  scales,
  ChampagneToggleProps,
  HandleProps,
  InputProps,
  ScaleKeys,
} from "./types";

const scaleKeyValues = {
  sm: {
    handleHeight: "16px",
    handleWidth: "16px",
    handleLeft: "2px",
    handleTop: "2px",
    checkedLeft: "calc(100% - 18px)",
    toggleHeight: "20px",
    toggleWidth: "36px",
  },
  md: {
    handleHeight: "26px",
    handleWidth: "26px",
    handleLeft: "3px",
    handleTop: "3px",
    checkedLeft: "calc(100% - 30px)",
    toggleHeight: "32px",
    toggleWidth: "56px",
  },
  lg: {
    handleHeight: "32px",
    handleWidth: "32px",
    handleLeft: "4px",
    handleTop: "4px",
    checkedLeft: "calc(100% - 36px)",
    toggleHeight: "40px",
    toggleWidth: "72px",
  },
};

const getScale =
  (property: ScaleKeys) =>
  ({ scale = scales.LG }: ChampagneToggleProps) => {
    return scaleKeyValues[scale][property];
  };

export const ChampagneStack = styled.div<HandleProps>`
  position: relative;
  display: inline-block;

  &:label:before {
    content: none;
  }

  .champagnes {
    position: absolute;
    transition: 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  .champagne {
    background: #e27c31;
    border-radius: 50%;
    width: ${getScale("champagneSize")};
    height: ${getScale("champagneSize")};
    position: absolute;
    transition: 0.4s ease;
    top: 2px;
    left: 4px;
    box-shadow: 0 ${getScale("champagneThickness")} 0
      ${getScale("champagneThickness")} #fbbe7c;
  }

  .champagne:nth-child(1) {
    background: ${({ theme }) => theme.champagneToggle.handleBackground};
    box-shadow: 0 ${getScale("champagneThickness")} 0
      ${getScale("champagneThickness")}
      ${({ theme }) => theme.champagneToggle.handleShadow};
  }

  .champagne:nth-child(2) {
    left: 0;
    top: ${getScale("champagneTwoOffset")};
    transform: scale(0);
    transition: 0.2s ease 0.2s;
  }

  .champagne:nth-child(3) {
    top: ${getScale("champagneThreeOffset")};
    transform: scale(0);
    transition: 0.2s ease 0.2s;
  }

  .champagne:nth-child(3):before,
  .champagne:nth-child(3):after {
    content: "";
    position: absolute;
    background: #ef8927;
    border-radius: 20px;
    width: 50%;
    height: 20%;
  }

  .champagne:nth-child(3):before {
    top: ${getScale("butterSmearOneTop")};
    left: ${getScale("butterSmearOneLeft")};
  }

  .champagne:nth-child(3):after {
    top: ${getScale("butterSmearTwoTop")};
    right: ${getScale("butterSmearTwoRight")};
  }

  .butter {
    width: ${getScale("butterWidth")};
    height: ${getScale("butterHeight")};
    background: #fbdb60;
    top: ${getScale("butterTop")};
    left: ${getScale("butterLeft")};
    position: absolute;
    border-radius: ${getScale("butterRadius")};
    box-shadow: 0 ${getScale("butterThickness")} 0
      ${getScale("butterThickness")} #d67823;
    transform: scale(0);
    transition: 0.2s ease;
  }
`;

export const ChampagneInput = styled.input<InputProps>`
  height: 40px;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  width: 40px;

  &:focus + label {
    box-shadow: ${({ theme }) => theme.shadows.focus};
  }

  &:checked + label .champagnes {
    transform: translateX(${getScale("travelDistance")});
  }

  &:checked + label .champagne:nth-child(1) {
    background: #e27c31;
    box-shadow: 0 ${getScale("champagneThickness")} 0
      ${getScale("champagneThickness")} #fbbe7c;
    transition-delay: 0.2s;
  }

  &:checked + label .champagne:nth-child(2) {
    transform: scale(1);
    transition-delay: 0.2s;
  }

  &:checked + label .champagne:nth-child(3) {
    transform: scale(1);
    transition-delay: 0.4s;
  }

  &:checked + label .butter {
    transform: scale(1);
    transition-delay: 0.6s;
  }
`;

export const ChampagneLabel = styled.label<ChampagneToggleProps>`
  width: ${getScale("toggleWidth")};
  height: ${getScale("toggleHeight")};
  background: ${({ theme, checked }) =>
    theme.colors[checked ? "success" : "input"]};
  box-shadow: ${({ theme }) => theme.shadows.inset};
  display: inline-block;
  border-radius: 50px;
  position: relative;
  transition: all 0.3s ease;
  transform-origin: 20% center;
  cursor: pointer;
`;
